    width: $button-width;
    height: auto; // âœ… Permet dâ€™ajuster la hauteur automatiquement
    padding: 12px 24px;
    margin-bottom: 57px;
    transition: all 0.3s ease-in-out; // âœ… Animation fluide

    @include flex(center, center);

    &:hover {
        background-color: darken($color-button-hover, 5%); // âœ… LÃ©gÃ¨rement plus foncÃ© au survol
        box-shadow: 0px 6px 12px 0px $color-button-shadow; // âœ… Ombre plus marquÃ©e
        transform: scale(1.05); // âœ… Agrandissement lÃ©ger
        cursor: pointer; // âœ… Change le curseur pour une meilleure UX
    }

